import React from 'react';
import { Text } from 'core/src/typography/Text';
import { cssDecorator } from '@front-toolkit/utils';

import styles from './ButtonTransparent.styles.scss';

const cd = cssDecorator(styles);

export interface ButtonTransparentProps {
  /** Колбэк при клике */
  onClick: VoidFunction;

  /** Текст кнопки */
  label: string;

  /** Флаг видимости кнопки */
  isVisible?: boolean;

  /** Data атрибут для тестирования */
  dataTestId?: string;
}

const ButtonTransparent = ({
  onClick,
  label,
  dataTestId,
  isVisible = true
}: ButtonTransparentProps): React.ReactElement => (
  <button
    type="button"
    className={ cd('button_transparent', { hidden: !isVisible }) }
    onClick={ onClick }
    data-test-id={ dataTestId }
  >
    <Text size="small">{ label }</Text>
  </button>
);

export default ButtonTransparent;
